// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/sourceMaps.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, `
#shifts{
        border: 1px solid white;
        border-radius: 9px;
        padding: 7px;       
        margin-bottom: 10px;
}
#btShowAddShift{
        cursor: pointer;
}
#btShowAddShift, #shiftTime > input{
        border: 1px solid white;
        color: white;
        padding: 5px;
        border-radius: 50px;
        background-color: transparent;
}
#shiftTime{
        display: flex;
        justify-content: space-around;
        align-items: center;
}
#shiftTime p{
        margin: 0;
}
#nbrMax{
        display: flex;
        justify-content: space-between;
        align-items: center;
}
#nbrMax > input{
        width: 20px;
        height: 20px;
}
#nbrMax > p{
        margin: 0;
        margin-top: 7px;
        margin-bottom: 7px;
}
#nbrMax > input::-webkit-outer-spin-button,
    #nbrMax > input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
}
#nbrMax > input[type=number] {
        -moz-appearance: textfield;
}
#btZone{
        text-align: center;
}
#btZone input{
        border-radius: 50px;
        padding: 7px;
}
#shiftSubTitle{
        text-align: center;
}
`, "",{"version":3,"sources":["webpack://./src/components/agendaProg/shiftsFormGestion.vue"],"names":[],"mappings":";AAwFI;QACI,uBAAuB;QACvB,kBAAkB;QAClB,YAAY;QACZ,mBAAmB;AACvB;AACA;QACI,eAAe;AACnB;AACA;QACI,uBAAuB;QACvB,YAAY;QACZ,YAAY;QACZ,mBAAmB;QACnB,6BAA6B;AACjC;AACA;QACI,aAAa;QACb,6BAA6B;QAC7B,mBAAmB;AACvB;AACA;QACI,SAAS;AACb;AACA;QACI,aAAa;QACb,8BAA8B;QAC9B,mBAAmB;AACvB;AACA;QACI,WAAW;QACX,YAAY;AAChB;AACA;QACI,SAAS;QACT,eAAe;QACf,kBAAkB;AACtB;AACA;;QAEI,wBAAwB;QACxB,SAAS;AACb;AACA;QACI,0BAA0B;AAC9B;AACA;QACI,kBAAkB;AACtB;AACA;QACI,mBAAmB;QACnB,YAAY;AAChB;AACA;QACI,kBAAkB;AACtB","sourcesContent":["<template>\r\n    <p class=\"shiftSubTitle\">{{ `${shiftType} : ` }}</p>\r\n    <div v-if=\"shifts.length > 0\" v-for=\"(shift, index) in shifts\" id=\"shifts\">\r\n        <div id=\"shiftTime\">\r\n            <input type=\"time\" v-model=\"shift.startTime\" @change=\"emitUpdate\">\r\n            <p> - </p>    \r\n            <input type=\"time\" v-model=\"shift.endTime\" @change=\"emitUpdate\">\r\n        </div>\r\n        <div id=\"nbrMax\">\r\n            <p>Nbr max de bénévoles : </p>\r\n            <input type=\"number\" v-model=\"shift.maxUsers\" @change=\"emitUpdate\">\r\n        </div>\r\n        <div id=\"btZone\"> \r\n            <input type=\"button\" value=\"Supprimer\" @click=\"handleRemoveShift(index)\"> \r\n        </div>\r\n    </div>\r\n    <div v-if=\"shifts\" id=\"addedShifts\"></div>\r\n    <p id=\"btShowAddShift\" @click=\"handleAddShift()\"> + Ajouter </p>\r\n</template>\r\n<script>\r\n    import { ref, onMounted, watch, nextTick } from 'vue'\r\n\r\n    export default{\r\n        name: 'shiftsFormGestion',\r\n        props: {\r\n            type: {\r\n                type: String,\r\n                required: true\r\n            },\r\n            propsShifts: {\r\n                type: Array,\r\n                required: false\r\n            }\r\n        },\r\n        setup(props, { emit }) {\r\n            const shifts = ref([]) \r\n            const shiftType = props.type \r\n\r\n            const handleAddShift = () => {\r\n                const lastShift = shifts.value.length > 0 ? shifts.value[shifts.value.length - 1] : null\r\n                const startTimeOfNewShift = lastShift ? lastShift.endTime : '20:00'\r\n                const indexForType = shifts.value.length // incrementation de 1 pour le shift suivant dans le type\r\n                shifts.value.push({\r\n                    type: shiftType,\r\n                    startTime: startTimeOfNewShift,\r\n                    endTime: null,\r\n                    maxUsers: 2,\r\n                    indexForType: indexForType\r\n                });  \r\n                emitUpdate()              \r\n            }\r\n            const handleRemoveShift = (index) => {   \r\n                if (Array.isArray(shifts.value)){\r\n                    shifts.value.splice(index, 1);\r\n                    emitUpdate()\r\n                }            \r\n            }\r\n            const emitUpdate = () => {\r\n                emit('updateShifts', JSON.parse(JSON.stringify(shifts.value))) \r\n            }\r\n            onMounted(async() => {     \r\n                //await nextTick();\r\n                console.log(\"shiftsFormGestion props reception : \", props.propsShifts)\r\n                if(Array.isArray(props.propsShifts)){\r\n                    shifts.value = JSON.parse(JSON.stringify(props.propsShifts)) // Copie des valeurs si newShifts est un tableau     \r\n                }\r\n            })\r\n            const formatTime = (timeString) => {\r\n                return timeString.slice(0, 6)\r\n            }\r\n            watch(() => props.propsShifts, (newShifts) => {\r\n                if (Array.isArray(newShifts)) {\r\n                    shifts.value = JSON.parse(JSON.stringify(newShifts));\r\n                }\r\n            }, { immediate: true });\r\n            return {\r\n                shifts,\r\n                shiftType,\r\n                handleAddShift,\r\n                handleRemoveShift,\r\n                emitUpdate,\r\n                formatTime\r\n            }\r\n        },\r\n    }\r\n</script>\r\n\r\n<style>\r\n    #shifts{\r\n        border: 1px solid white;\r\n        border-radius: 9px;\r\n        padding: 7px;       \r\n        margin-bottom: 10px;\r\n    }\r\n    #btShowAddShift{\r\n        cursor: pointer;\r\n    }\r\n    #btShowAddShift, #shiftTime > input{\r\n        border: 1px solid white;\r\n        color: white;\r\n        padding: 5px;\r\n        border-radius: 50px;\r\n        background-color: transparent;\r\n    }\r\n    #shiftTime{\r\n        display: flex;\r\n        justify-content: space-around;\r\n        align-items: center;\r\n    }\r\n    #shiftTime p{\r\n        margin: 0;\r\n    }\r\n    #nbrMax{\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n    }\r\n    #nbrMax > input{\r\n        width: 20px;\r\n        height: 20px;\r\n    }\r\n    #nbrMax > p{\r\n        margin: 0;\r\n        margin-top: 7px;\r\n        margin-bottom: 7px;\r\n    }\r\n    #nbrMax > input::-webkit-outer-spin-button,\r\n    #nbrMax > input::-webkit-inner-spin-button {\r\n        -webkit-appearance: none;\r\n        margin: 0;\r\n    }\r\n    #nbrMax > input[type=number] {\r\n        -moz-appearance: textfield;\r\n    }\r\n    #btZone{\r\n        text-align: center;\r\n    }\r\n    #btZone input{\r\n        border-radius: 50px;\r\n        padding: 7px;\r\n    }\r\n    #shiftSubTitle{\r\n        text-align: center;\r\n    }\r\n</style>"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
