___CSS_LOADER_EXPORT___.push([module.id, `
#tableAgendaBenevol {
        border-collapse: collapse;
        font-family: sans-serif;
        font-size: 0.8rem;
        letter-spacing: 1px;
        margin: auto;
}
#tableAgendaBenevol caption {
        caption-side: bottom;
        padding: 10px;
        font-weight: bold;
}
#tableAgendaBenevol th,td {
        padding: 8px 10px;
        border: 1px solid black;
        text-align: center;
        vertical-align: center;
        font-weight: bold;
}
#tableAgendaBenevol td:last-of-type {
        text-align: center;
}
thead > tr > th{
        writing-mode: vertical-rl;
        background-color: #faedbc;
        color: black;
}
.enough{
        color: green;
}
.notEnough{
        color: red;
}
.firstname{
        background-color: #faedbc;
        color: black;
}
.userRow:hover td {
        background-color: #ebeff2;
        cursor: pointer;
}
.weekOne{
        background-color: #faedbc; /* #fdfde9; */
}
.weekTwo{
        background-color: #cbe4bf; /* #e2f0db; */
}
`, "",{"version":3,"sources":["webpack://./src/components/agendaBenevol/agendaBenevol.vue"],"names":[],"mappings":";AA6EI;QACI,yBAAyB;QACzB,uBAAuB;QACvB,iBAAiB;QACjB,mBAAmB;QACnB,YAAY;AAChB;AACA;QACI,oBAAoB;QACpB,aAAa;QACb,iBAAiB;AACrB;AACA;QACI,iBAAiB;QACjB,uBAAuB;QACvB,kBAAkB;QAClB,sBAAsB;QACtB,iBAAiB;AACrB;AACA;QACI,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,yBAAyB;QACzB,YAAY;AAChB;AACA;QACI,YAAY;AAChB;AACA;QACI,UAAU;AACd;AACA;QACI,yBAAyB;QACzB,YAAY;AAChB;AACA;QACI,yBAAyB;QACzB,eAAe;AACnB;AACA;QACI,yBAAyB,EAAE,aAAa;AAC5C;AACA;QACI,yBAAyB,EAAE,aAAa;AAC5C","sourcesContent":["<template>\r\n    <nav-bar />\r\n    <h1>Agenda benevol</h1>\r\n    <table id=\"tableAgendaBenevol\">\r\n        <thead class=\"thead\">\r\n            <tr>\r\n                <th scope=\"col\">Bénévole</th>\r\n                <th scope=\"col\">janvier</th>\r\n                <th scope=\"col\">février</th>\r\n                <th scope=\"col\">mars</th>\r\n                <th scope=\"col\">avril</th>\r\n                <th scope=\"col\">mai</th>\r\n                <th scope=\"col\">juin</th>\r\n                <th scope=\"col\">juillet</th>\r\n                <th scope=\"col\">aout</th>\r\n                <th scope=\"col\">septembre</th>\r\n                <th scope=\"col\">octobre</th>\r\n                <th scope=\"col\">novembre</th>\r\n                <th scope=\"col\">décembre</th>\r\n            </tr>\r\n        </thead>\r\n        <tbody>\r\n            <tr v-for=\"user in statisticsOfUsers\" class=\"userRow\">\r\n                <td scope=\"row\" class=\"firstname\">{{ user.firstname }}</td>\r\n                <td scope=\"row\" v-for=\"(data, dataIndex) in user\" \r\n                    :class=\"[setColumnClass(dataIndex), data >= 2 ? 'enough' :  data < 2 ? 'notEnough' : 'problem']\"\r\n                    :hidden=\"!isItInt(data)\"\r\n                >{{ data }}</td>\r\n            </tr>\r\n        </tbody>\r\n   </table>\r\n</template>\r\n<script>\r\n    import { ref, onMounted } from 'vue'; \r\n    import navbar from '../navbar.vue';\r\n    import { getStatisticsOfUsersRequest } from '../../helpers/requests'\r\n    export default{\r\n        name: 'agendaBenevol',\r\n        components: {\r\n            'nav-bar': navbar,\r\n        },\r\n        setup(){ \r\n            const statisticsOfUsers = ref([])\r\n            onMounted(async() => { \r\n                try{\r\n                    const userStats = (await getStatisticsOfUsersRequest()).data\r\n                    console.log(\"userStats : \", userStats)\r\n                    statisticsOfUsers.value = userStats\r\n                    console.log(\"statisticsOfUsers : \", statisticsOfUsers.value)\r\n                }catch(error){\r\n                    console.log(error)\r\n                }\r\n            })\r\n            const isItInt = (varToCheck) => {\r\n                return typeof varToCheck === \"number\" && Number.isInteger(varToCheck)\r\n            }\r\n            const setColumnClass = (nbrMonth) => {\r\n                let classToReturn = null\r\n                const nbrToCheck = parseInt(nbrMonth, 10)\r\n                if(nbrToCheck && nbrToCheck % 2 === 0){\r\n                    classToReturn = 'weekOne'\r\n                }else if( nbrToCheck && nbrToCheck % 2 !== 0){\r\n                    classToReturn = 'weekTwo'\r\n                }\r\n                console.log(\"classToReturn : \", classToReturn)\r\n                return classToReturn\r\n            }\r\n            return {\r\n                statisticsOfUsers,\r\n                isItInt,\r\n                setColumnClass\r\n            }\r\n        },\r\n    }\r\n</script>\r\n\r\n<style>\r\n    #tableAgendaBenevol {\r\n        border-collapse: collapse;\r\n        font-family: sans-serif;\r\n        font-size: 0.8rem;\r\n        letter-spacing: 1px;\r\n        margin: auto;\r\n    }\r\n    #tableAgendaBenevol caption {\r\n        caption-side: bottom;\r\n        padding: 10px;\r\n        font-weight: bold;\r\n    }\r\n    #tableAgendaBenevol th,td {\r\n        padding: 8px 10px;\r\n        border: 1px solid black;\r\n        text-align: center;\r\n        vertical-align: center;\r\n        font-weight: bold;\r\n    }\r\n    #tableAgendaBenevol td:last-of-type {\r\n        text-align: center;\r\n    }\r\n    thead > tr > th{\r\n        writing-mode: vertical-rl;\r\n        background-color: #faedbc;\r\n        color: black;\r\n    }\r\n    .enough{\r\n        color: green;\r\n    }\r\n    .notEnough{\r\n        color: red;\r\n    }\r\n    .firstname{\r\n        background-color: #faedbc;\r\n        color: black;\r\n    }\r\n    .userRow:hover td {\r\n        background-color: #ebeff2;\r\n        cursor: pointer;\r\n    }    \r\n    .weekOne{\r\n        background-color: #faedbc; /* #fdfde9; */\r\n    }\r\n    .weekTwo{\r\n        background-color: #cbe4bf; /* #e2f0db; */\r\n    }\r\n</style>"],"sourceRoot":""}]);
