      console.log(\"emit parent reception !\")\r\n                isAddNewShowFormOpen.value = false\r\n            }\r\n            onMounted(async()=>{\r\n                const allShows = await getAllRecordedSoirees()\r\n                allRecordedShow.value = allShows.data\r\n                console.log(\"allRecordedShow.value : \", allShows.data)\r\n            })\r\n            return {\r\n                isAddNewShowFormOpen,\r\n                handleOpenAddNewShowForm,\r\n                handleCloseForm,\r\n                allRecordedShow\r\n            }\r\n        },\r\n    }\r\n</script>\r\n\r\n<style>\r\n#componentAddNewShow, #modifForm{\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    width: 250px;\r\n    margin: auto;\r\n    border: 1px solid white;\r\n    border-radius: 15px;\r\n    height: 70vh;\r\n    overflow-y: scroll; \r\n    scrollbar-width: none;\r\n}\r\n#componentAddNewShow::-webkit-scrollbar {\r\n    display: none; \r\n}\r\n#recordedShowsZone{\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 10px;\r\n    flex-wrap: wrap;\r\n}\r\n#recordedShowsZone > div{\r\n    padding: 15px;\r\n    display: flex;\r\n    justify-content: center;\r\n    gap: 20px;\r\n    flex-wrap: wrap;\r\n    width: min-content;\r\n}\r\n</style>"],"sourceRoot":""}]);
