#navBar{
        width: 100%;
        height: 10vh;
        background-color: black;
        display: flex;
        justify-content: left;
        gap: 20px;
        border: none;
}
.navItem{
        display: flex;
        align-items: center;
}
.navLink {
        color: white;
        text-decoration: none;
        padding: 7px;
        background-color: transparent;
}
.navLink:hover{
        cursor: pointer;
        background-color: transparent;
}
