___CSS_LOADER_EXPORT___.push([module.id, `
#calendarSection{
        position: relative;
        width: 100%;
        height: 90vh;
}
#monthPicker{
        width: 100%;
        display: flex;
        justify-content: center;
        gap: 20px;
        margin-bottom: 25px;
}
#aMonthBack, #aMonthNext{
        width: 35px;
        height: 35px;
        border: none;
        border-radius: 20px;
        background-color: black;
        color: white;
        text-align: center;
        align-items: center;
}
#calendar{
        margin: auto;
        border-collapse: separate; 
        border-spacing: 10px;
}
#titleArray{
        font-weight: bolder;        
        text-align: center;
        align-items: center;
}
#titleArray > td{
        width: 75px;
        height:25px;
        padding: 5px;
}
.arrayRaw > td{
        height: 75px;
        width: 75px;
        border-radius: 10px;
        text-align: center;
        align-items: center;
        padding: 5px;
}
.arrayRaw > td:hover{
        border: 1px solid white;
        cursor: pointer;
}
   /* .notInTheMonth{
        background-color: gray;
    }
    .inTheMonth{
        background-color: blueviolet;
    }*/
#calendarContainer{
        display: flex;
        justify-content: space-between;
}
.closedDate{
        background-color: gray;
        border: none;
}
.shiftedNormaleDate{
        background-image: linear-gradient(to bottom right, white, blue, blue);
        border: none;
}
.shiftedShowDate{
        background-image: linear-gradient(to bottom right, white, green, green);
        border: none;
}
.normaleDate{
        background-color: blue;
        border: none;
}
.showDate{
        background-color: green;
        border: none;
}
.reunionDate{
        background-color: aquamarine;
        border: none;
}
.today{
        background-color: red;
        border: 2px solid white;
}
.notInMonth{
        background-color: transparent;
        border: 1px solid white;
}
.shiftedDay{
        background-color: red;
}
