___CSS_LOADER_EXPORT___.push([module.id, `
#passwordBtsZone{
        display: flex;
        justify-content: center;
        gap: 10px;
}
#allMyShiftsContainer{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: top;
        gap: 15px;
        margin-top: 15px;
        margin-bottom: 15px;
        color: white;
}
#allMyShiftsDiv{
        width: 25%;
        padding: 15px;
        border: none;
        border-radius: 5px;
        background-color: #39393b;
        border: 1px solid white;
}
#oneOfMyShift{
        border-radius: 5px;
        border:none;
        padding: 10px;
        background-color: #75757a;
        margin-bottom: 10px;
}
#oneOfMyShift input{
        text-align: center;
        background-color: #39393b;
        color: white;
        border: none;
        border-radius: 5px;
        padding: 5px;
}
#oneOfMyShift > div{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
}#userInputZone{
        display: flex;
        flex-direction: column;
        width: 50%;
}
#userInputZone{
        width: 50%;
}
