___CSS_LOADER_EXPORT___.push([module.id, `
#modifyForm{
        z-index: 99000;
        position:absolute;
        width: 30%;
        height: 100vh;
        top: 0;
        right: 0;
        background-color: #292929;
        overflow-y: auto;
}
#modifyForm::-webkit-scrollbar {
        display: none;
}
#popupShow{      
        z-index: 9000;
        background-color: #212121;
        color: white;
        display: flex;
}
#popupShow{
        height: 100vh;
        position: fixed;
        width: 30%;
        top: 0;
        right: 0;
        background-color: #212121;
        color: white;
        display: flex;
        flex-direction: column;
        padding: 20px;
        padding-top: 0;
        overflow-y: auto;
}
#popupShow::-webkit-scrollbar {
        display: none;
}
#shiftsZone{
        display: flex;
        flex-direction: column;
}
#shiftsZone > h7 {     
        width: 100%;
        background-color: #4a4a4a;
        margin-top: 15px;
        margin-bottom: 15px;
        padding-left: 15px;
}
#shiftUsersZone{
        width: 100%;
        display: flex;
        justify-content: center;
        gap: 10px;
        border-bottom: #4a4a4a 1px solid ;
        padding-bottom: 10px;
}
#userZone{
        color: #212121;
        background-color: #F9F871;
        border-radius: 20px;
        vertical-align: center;
        padding: 5px;
}
#userZone > p{
        vertical-align: center;
        margin: 0;
}
#oneShiftZone{
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-bottom: #4a4a4a 1px solid ;
        width: 100%;
        padding-bottom: 10px;
}
#subscribeToShift{
        width: 75%;
        border: 1px solid #4a4a4a;
        border-radius: 20px;
        background-color: #292929;
        color: white;
        margin: auto;
        margin-top: 15px;
}
#closePopupShow{
        width: 30px;
        height: 30px;
        right: 20px;
        border: none;
        border-radius: 50px;
        background-color: #4a4a4a;
        color: #212121;
}
#openModify{
        background-color: #4a4a4a;
        color: white;
        border: none;
        border-radius: 50px;
        padding: 5px;
        text-align: center;
        margin: 7px;
        z-index: 99999;
}
#closeModify{
        width: 35px;
        height: 35px;
        border-radius: 50px;
        border: none;
        background-color: red;
        color: white;
        z-index: 999991;
        position: absolute;
        top: 10px;
        right: 10px;
}
#openingZone{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 3px;
}
#openingZone > div{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 3px;
}
.nameUser{
        padding: 4px;
        border: 1px solid white;
        border-radius: 50px;
}
`, "",{"version":3,"sources":["webpack://./src/components/calendar/popupShow.vue"],"names":[],"mappings":";AAsOI;QACI,cAAc;QACd,iBAAiB;QACjB,UAAU;QACV,aAAa;QACb,MAAM;QACN,QAAQ;QACR,yBAAyB;QACzB,gBAAgB;AACpB;AACA;QACI,aAAa;AACjB;AACA;QACI,aAAa;QACb,yBAAyB;QACzB,YAAY;QACZ,aAAa;AACjB;AACA;QACI,aAAa;QACb,eAAe;QACf,UAAU;QACV,MAAM;QACN,QAAQ;QACR,yBAAyB;QACzB,YAAY;QACZ,aAAa;QACb,sBAAsB;QACtB,aAAa;QACb,cAAc;QACd,gBAAgB;AACpB;AACA;QACI,aAAa;AACjB;AACA;QACI,aAAa;QACb,sBAAsB;AAC1B;AACA;QACI,WAAW;QACX,yBAAyB;QACzB,gBAAgB;QAChB,mBAAmB;QACnB,kBAAkB;AACtB;AACA;QACI,WAAW;QACX,aAAa;QACb,uBAAuB;QACvB,SAAS;QACT,iCAAiC;QACjC,oBAAoB;AACxB;AACA;QACI,cAAc;QACd,yBAAyB;QACzB,mBAAmB;QACnB,sBAAsB;QACtB,YAAY;AAChB;AACA;QACI,sBAAsB;QACtB,SAAS;AACb;AACA;QACI,aAAa;QACb,6BAA6B;QAC7B,mBAAmB;QACnB,iCAAiC;QACjC,WAAW;QACX,oBAAoB;AACxB;AACA;QACI,UAAU;QACV,yBAAyB;QACzB,mBAAmB;QACnB,yBAAyB;QACzB,YAAY;QACZ,YAAY;QACZ,gBAAgB;AACpB;AACA;QACI,WAAW;QACX,YAAY;QACZ,WAAW;QACX,YAAY;QACZ,mBAAmB;QACnB,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,yBAAyB;QACzB,YAAY;QACZ,YAAY;QACZ,mBAAmB;QACnB,YAAY;QACZ,kBAAkB;QAClB,WAAW;QACX,cAAc;AAClB;AACA;QACI,WAAW;QACX,YAAY;QACZ,mBAAmB;QACnB,YAAY;QACZ,qBAAqB;QACrB,YAAY;QACZ,eAAe;QACf,kBAAkB;QAClB,SAAS;QACT,WAAW;AACf;AACA;QACI,aAAa;QACb,sBAAsB;QACtB,uBAAuB;QACvB,mBAAmB;QACnB,QAAQ;AACZ;AACA;QACI,WAAW;QACX,aAAa;QACb,uBAAuB;QACvB,mBAAmB;QACnB,QAAQ;AACZ;AACA;QACI,YAAY;QACZ,uBAAuB;QACvB,mBAAmB;AACvB","sourcesContent":[" <template>\r\n    <input id=\"closeModify\" type=\"button\" value=\"X\" v-if=\"areModifsOpenend\" @click=\"handleCloseModifs\">\r\n    <showForm id=\"modifyForm\" :typeOfForm=\"'modify'\" :dataOfShow=\"allData\" v-if=\"areModifsOpenend\" @modifsInformationsDone=\"updateShifts\" @closeShowForm=\"handleCloseModifs\"/>\r\n    <div id=\"popupShow\">        \r\n        <input id=\"closePopupShow\" type=\"button\" value=\"X\" @click=\"handleClosePopupShow\">\r\n        <input id=\"openModify\" type=\"button\" value=\"Modifier\" v-if=\"!areModifsOpenend\" @click=\"openModifs\"/>\r\n        <general-info v-if=\"allData\" :dataInfos=\"generalInfoData\" />\r\n        <div id=\"openingZone\" v-if=\"allData.status !== 'fermé' && allData.status !== 'réunion'\">\r\n            <h7>Ouverture du bar</h7>\r\n            <div id=\"openingUsersZone\">\r\n                <p v-if=\"openingUser.length > 0\" v-for=\"user in openingUser\" class=\"nameUser\">{{ user.firstname }}</p>    \r\n                <p v-else>Il n'y a personne :(</p>      \r\n            </div>   \r\n            <div id=\"openingBts\">                       \r\n                <input v-if=\"!isUserSubscribedToOpening\" @click=\"subscribeToOpening\" type=\"button\" value=\"S'inscrire\">\r\n                <input v-else type=\"button\" value=\"Se désinscrire\" @click=\"handleRemoveUserFromExtraTime(1)\">\r\n            </div>         \r\n        </div>\r\n        <shift-zone v-if=\"barData && barData.length > 0\" :shiftInfos=\"barData\" />\r\n        <shift-zone v-if=\"entreeData && entreeData.length > 0\" :shiftInfos=\"entreeData\" />\r\n        <shift-zone v-if=\"parkingData && parkingData.length > 0\" :shiftInfos=\"parkingData\" />\r\n        <div id=\"closureZone\">\r\n            <h7>Fermeture du bar</h7>\r\n            <p v-if=\"closureUser\" v-for=\"user in closureUser\">{{ user }}</p>\r\n            <p v-else>Il n'y a personne :(</p>\r\n            <input type=\"button\" value=\"S'inscrire\">\r\n            <input type=\"button\" value=\"Se désinscrire\">\r\n        </div>\r\n    </div>\r\n</template> \r\n\r\n<script>\r\nimport { ref, onMounted, watch, nextTick} from 'vue';\r\nimport { \r\n    getAllShiftsOfAshowRequest, \r\n    subscribeUserToExtraTime,\r\n    getUsersOfOpeningAndClosureOfShow,\r\n    getShowByIdRequest\r\n} from '../../helpers/requests.js'\r\nimport generalInfo from './generalInfo.vue';\r\nimport shiftZone from './shiftZone.vue';\r\nimport showForm from '../agendaProg/showForm.vue'\r\nimport { checkUserConnexion } from '../../helpers/checks.js';\r\n\r\nexport default {\r\n    name: 'popupShow',\r\n    components: {\r\n        'general-info': generalInfo,\r\n        'shift-zone': shiftZone,\r\n        'showForm': showForm\r\n    },\r\n    props: {\r\n        dataOfDate: {\r\n            type: Object,\r\n            required: true\r\n        }\r\n    },\r\n    setup(props, { emit }) {\r\n        const date = ref('')\r\n        const allData = ref({})\r\n        const showObject = ref('')\r\n        const barData = ref([])\r\n        const entreeData = ref([])\r\n        const parkingData =ref([])\r\n        const areModifsOpenend = ref(false)\r\n        const openingUser = ref({})\r\n        const closureUser = ref({})\r\n        const isUserSubscribedToOpening = ref(false)\r\n        const isUserSubscribedToClosure = ref(false)\r\n        const generalInfoData = ref({})\r\n        onMounted(async() => {                 \r\n            //await nextTick();\r\n            const mountedData = props.dataOfDate \r\n            allData.value = mountedData;  \r\n            setGeneralInfoData(mountedData)\r\n            formatShiftsForComponent()\r\n            updateOpeningAndClosureUsers(allData.value.laBilleShowId)\r\n\r\n        });\r\n        watch(() => props.dataOfDate, (newVal) => {\r\n            allData.value = newVal  \r\n            setGeneralInfoData(newVal)\r\n            formatShiftsForComponent()\r\n            updateOpeningAndClosureUsers(newVal.laBilleShowId)\r\n        });\r\n        const setGeneralInfoData = (data) => {\r\n            generalInfoData.value = {\r\n                laBilleShowId: data.laBilleShowId,\r\n                date: data.date,\r\n                status: data.status,\r\n                soundEngineer: data.soundEngineer,\r\n                showResponsable: data.showResponsable,\r\n                notes: data.notes\r\n            }\r\n        }\r\n        const handleClosePopupShow = () => {\r\n            emit('closePopup');\r\n        };\r\n        const handleCloseShowForm = () => {\r\n            areModifsOpenend.value = false\r\n        }\r\n        const formatShiftsForComponent = () => {\r\n            if(allData.value.status === 'normale' || allData.value.status === 'soirée' ){ // si show pas enregistré dans db & non fermé on record     \r\n                barData.value = allData.value.shifts ? allData.value.shifts.filter(shift => shift.type === 'bar') : []\r\n                entreeData.value = allData.value.shifts ? allData.value.shifts.filter(shift => shift.type === 'entree') : []\r\n                parkingData.value = allData.value.shifts ? JSON.parse(JSON.stringify(allData.value.shifts.filter(shift => shift.type === 'parking'))) : []\r\n            }else if(allData.value.status === 'réunion'){\r\n                barData.value = allData.value.shifts ? allData.value.shifts.filter(shift => shift.type === 'bar') : []   \r\n                entreeData.value = []\r\n                parkingData.value = []\r\n            }else{\r\n                barData.value = []\r\n                entreeData.value = []\r\n                parkingData.value = []\r\n            }\r\n        }\r\n        const openModifs = () => {\r\n            areModifsOpenend.value = true\r\n        }\r\n        const handleCloseModifs = () => {\r\n            areModifsOpenend.value = false\r\n        }\r\n        const updateShifts = async () => {\r\n            const idShow = allData.value.laBilleShowId\r\n            if (idShow) updateOpeningAndClosureUsers(idShow)\r\n            if (idShow) updateGeneralInfo(idShow)\r\n            const data = await getAllShiftsOfAshowRequest(idShow)\r\n            if(data.msg === 'success_noData'){\r\n                barData.value = []\r\n                entreeData.value = []\r\n                parkingData.value = []\r\n            }else if(data.msg === 'success_containData'){\r\n                barData.value = data.data.bar || []\r\n                entreeData.value = data.data.entree || []\r\n                parkingData.value = data.data.parking || []\r\n                allData.value.shifts = [ ...barData.value, ...entreeData.value, ...parkingData.value ]\r\n            }     \r\n        }\r\n        const updateGeneralInfo = async (idShow) => {\r\n            if(!idShow) return\r\n            const newData = await getShowByIdRequest(idShow)\r\n            console.log(\"GENERALINFO : \", newData)\r\n            if(newData.msg === 'success' && newData.data){\r\n                console.log(\"SUCESSSSSSSS\")\r\n                const updatedData = newData.data\r\n                setGeneralInfoData(updatedData)          \r\n                await nextTick()\r\n            }else{                \r\n                console.log(\"FAILLLLLLLLLLLLLL\")\r\n            }\r\n            \r\n        }\r\n        const handleRemoveUserFromExtraTime = (type) => {\r\n            const dataToDelete = {\r\n                idShow: generalInfoData.value.laBilleShowId,\r\n                type: type,\r\n                idUser: checkUserConnexion().idUser\r\n            }\r\n            console.log(\"dataToDelete : \", dataToDelete)\r\n        }\r\n        const updateOpeningAndClosureUsers = async (idShow) => {\r\n            isUserSubscribedToOpening.value = false\r\n            isUserSubscribedToClosure.value = false\r\n            const users = await getUsersOfOpeningAndClosureOfShow(idShow)\r\n            if(users.msg !== 'success' || !users.data){\r\n                openingUser.value = {} \r\n                return\r\n            }         \r\n            openingUser.value = users.data.filter((user) => user.type === '1')\r\n            isUserSubscribedToOpening.value = false\r\n            isUserSubscribedToOpening.value = openingUser.value.map((user) => {\r\n                if (user.idUser === checkUserConnexion().idUser) {\r\n                    return true;\r\n                }\r\n            })\r\n                //return { idUser: user.idUser, firstname: user.firstname };    \r\n           /* closureUser.value = users.data\r\n                .filter((user) => user.type === '0')\r\n                .map((user) => {\r\n                    if (user.idUser === checkUserConnexion().idUser) {\r\n                        isUserSubscribedToClosure.value = true;\r\n                    }\r\n                    return { idUser: user.idUser, firstname: user.firstname };\r\n                });\r\n            }*/\r\n            await nextTick(); \r\n        }\r\n        // inscriptions / desinscription aux ouverture / fermetures\r\n        const subscribeToOpening = async () => { \r\n            const idShow = allData.value.laBilleShowId\r\n            const idUser = checkUserConnexion().idUser\r\n            const type = 1\r\n            const checksubscribe = await subscribeUserToExtraTime(idShow, idUser, type)\r\n            let msg;\r\n            if(checksubscribe.msg === 'error2'){\r\n                msg = 'Tu es dejà inscrit ;)' \r\n                window.alert(msg)\r\n            } \r\n            else if(checksubscribe.msg === 'success'){\r\n                msg = 'Tu es bien inscrit ! Merci à toi :)'\r\n                updateOpeningAndClosureUsers(idShow)\r\n                window.alert(msg)\r\n            }\r\n        }\r\n        return {\r\n            date,\r\n            allData,\r\n            handleClosePopupShow,\r\n            showObject,\r\n            barData,\r\n            entreeData,\r\n            parkingData,\r\n            openModifs,\r\n            areModifsOpenend,\r\n            handleCloseModifs,\r\n            handleCloseShowForm,\r\n            updateShifts,\r\n            openingUser,\r\n            closureUser,\r\n            subscribeToOpening,\r\n            isUserSubscribedToOpening,\r\n            isUserSubscribedToClosure,\r\n            generalInfoData,\r\n            handleRemoveUserFromExtraTime\r\n        };\r\n    }\r\n};\r\n</script>\r\n\r\n<style>\r\n    #modifyForm{\r\n        z-index: 99000;\r\n        position:absolute;\r\n        width: 30%;\r\n        height: 100vh;\r\n        top: 0;\r\n        right: 0;\r\n        background-color: #292929;\r\n        overflow-y: auto;\r\n    }\r\n    #modifyForm::-webkit-scrollbar {\r\n        display: none;\r\n    }\r\n    #popupShow{      \r\n        z-index: 9000;\r\n        background-color: #212121;\r\n        color: white;\r\n        display: flex;\r\n    }\r\n    #popupShow{\r\n        height: 100vh;\r\n        position: fixed;\r\n        width: 30%;\r\n        top: 0;\r\n        right: 0;\r\n        background-color: #212121;\r\n        color: white;\r\n        display: flex;\r\n        flex-direction: column;\r\n        padding: 20px;\r\n        padding-top: 0;\r\n        overflow-y: auto;\r\n    }\r\n    #popupShow::-webkit-scrollbar {\r\n        display: none;\r\n    }\r\n    #shiftsZone{\r\n        display: flex;\r\n        flex-direction: column;\r\n    }\r\n    #shiftsZone > h7 {     \r\n        width: 100%;\r\n        background-color: #4a4a4a;\r\n        margin-top: 15px;\r\n        margin-bottom: 15px;\r\n        padding-left: 15px;\r\n    }\r\n    #shiftUsersZone{\r\n        width: 100%;\r\n        display: flex;\r\n        justify-content: center;\r\n        gap: 10px;\r\n        border-bottom: #4a4a4a 1px solid ;\r\n        padding-bottom: 10px;\r\n    }\r\n    #userZone{\r\n        color: #212121;\r\n        background-color: #F9F871;\r\n        border-radius: 20px;\r\n        vertical-align: center;\r\n        padding: 5px;\r\n    }\r\n    #userZone > p{\r\n        vertical-align: center;\r\n        margin: 0;\r\n    }\r\n    #oneShiftZone{\r\n        display: flex;\r\n        justify-content: space-around;\r\n        align-items: center;\r\n        border-bottom: #4a4a4a 1px solid ;\r\n        width: 100%;\r\n        padding-bottom: 10px;\r\n    }\r\n    #subscribeToShift{\r\n        width: 75%;\r\n        border: 1px solid #4a4a4a;\r\n        border-radius: 20px;\r\n        background-color: #292929;\r\n        color: white;\r\n        margin: auto;\r\n        margin-top: 15px;\r\n    }\r\n    #closePopupShow{\r\n        width: 30px;\r\n        height: 30px;\r\n        right: 20px;\r\n        border: none;\r\n        border-radius: 50px;\r\n        background-color: #4a4a4a;\r\n        color: #212121;\r\n    }\r\n    #openModify{\r\n        background-color: #4a4a4a;\r\n        color: white;\r\n        border: none;\r\n        border-radius: 50px;\r\n        padding: 5px;\r\n        text-align: center;\r\n        margin: 7px;\r\n        z-index: 99999;\r\n    }\r\n    #closeModify{\r\n        width: 35px;\r\n        height: 35px;\r\n        border-radius: 50px;\r\n        border: none;\r\n        background-color: red;\r\n        color: white;\r\n        z-index: 999991;\r\n        position: absolute;\r\n        top: 10px;\r\n        right: 10px;\r\n    }\r\n    #openingZone{\r\n        display: flex;\r\n        flex-direction: column;\r\n        justify-content: center;\r\n        align-items: center;\r\n        gap: 3px;\r\n    }\r\n    #openingZone > div{\r\n        width: 100%;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        gap: 3px;\r\n    }\r\n    .nameUser{\r\n        padding: 4px;\r\n        border: 1px solid white;\r\n        border-radius: 50px;\r\n    }\r\n</style>\r\n\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
